* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.container {
  width: 95vw;
  margin: auto;
  max-width: 1280px;
  padding: 50px 30px;
}
.nav-container {
  width: 95vw;
  max-width: 1280px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding: 10px 30px;
  font-size: 30px;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

h1 {
  text-align: center;
  font-size: 4rem;
  margin-top: 15px;
  margin-bottom: 40px;
}
.products-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, auto));
  grid-gap: 10px;
}
.product-image {
  height: 280px;
}
.product {
  border: 1px solid #eee;
}

.product-body {
  padding: 10px;
  border-top: 1px solid beige;
}

.product-body h4 {
  font-size: 20px;
  margin-bottom: 10px;
}

.product-body h4 a {
  text-decoration: none;
  color: #000;
}

.product-footer {
  padding: 10px;
  background: beige;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
}
.product-wrapper {
  display: grid;
  grid-template-columns: repeat(2, minmax(500px, auto));
}
@media screen and (max-width: 1024px) {
  .product-wrapper {
    grid-template-columns: repeat(1, minmax(auto, auto));
  }
  .product-details {
    margin-top: 20px !important;
    padding-left: 0px !important;
  }
}

.product-details {
  padding: 0px 20px 20px 20px;
}

.product-details h1 {
  font-size: 41px;
  text-align: left;
  margin: 0px 0px 10px 0px;
}

.quantity {
  margin-top: 20px;
  margin-bottom: 20px;
  display: grid;
  max-width: 250px;
  grid-template-columns: repeat(auto-fit, minmax(50px, auto));
  align-items: center;
  gap: 10px;
  width: 50%;
}

.quantity button {
  background: #eee;
  border: none;
  padding: 5px 12px;
  font-size: 22px;
  border-radius: 2px;
  cursor: pointer;
}

.quantity input {
  padding: 5px 8px;
  width: 40px;
  border: none;
  outline: none;
  text-align: center;
}

.quantity span {
  text-transform: capitalize;
  color: #6a6a6a;
}

.add-to-cart-btns {
  display: flex;
  gap: 10px;
}

.add-to-cart-btns button {
  border: none;
  padding: 15px 50px;
  border-radius: 5px;
  font-size: 18px;
  text-transform: capitalize;
  cursor: pointer;
  margin-top: 20px;
}
nav {
  background: black;
  color: #fff;
  padding: 10px;
}
.shopping-cart {
  position: relative;
}

span.carts-quantity {
  position: absolute;
  background: orange;
  font-size: 12px;
  width: 25px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -10px;
  left: 20px;
}